.aboutme_text {
    color: darkslategray !important;
    font: smaller !important;
    line-height: 22px !important;
    white-space: pre-line !important;
}

.resume_timeline .timeline_content .timeline_firm {
    font-size: 16px !important;
    font-weight: 500 !important;
}

.resume_timeline .timeline_content .timeline_date {
    color: rgb(42, 155, 155);
}

.resume_timeline .timeline_content .timeline_desc {
    color: grey;
    font-size: small;
    margin-top: 6px;
}

.service {
    height: 100%;
    text-align: center;
}

.service .service_title {
    font-size: 16px !important;
    font-weight: 500 !important;
    line-height: 35px !important;
    text-align: center;
}

.service .service_desc {
    color: #989898 !important;
    border-color: var(--main-color) !important;
    margin-right: 5px !important;
    text-align: center;
}

.service_icon>.MuiSvgIcon-root {
    font-size: 46px !important;
    margin-left: 0 !important;
    font-weight: 300 !important;
    color: darkslategray !important;
}

.skill {
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08) !important;
    min-height: 186px;
    height: 100%;
}

.skill .skill_title {
    font-size: 14px !important;
    font-weight: 500 !important;
    line-height: 35px !important;
}

.skill .skill_type {
    display: flex;
    align-items: center;
    color: #989898 !important;
    font-weight: 400 !important;
    line-height: 18px !important;
}

.skill .skill_type .timeline_dot {
    color: black !important;
    border-color: var(--main-color) !important;
    padding: 2px !important;
    margin-right: 5px !important;
}

.contactdetail_item {
    font-size: 14px !important;
    color: #787878;
}

.contactdetail_item>span {
    color: black !important;
    font-weight: 500;
}